module.exports = "@import url(\"https://fonts.googleapis.com/css?family=Neucha\");\n@import url(\"https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,700&subset=cyrillic\");\n.post {\n  background: #FFFFFF;\n  height: 180px;\n  padding: 15px;\n  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);\n  -webkit-transform: perspective(1px) translateZ(0);\n          transform: perspective(1px) translateZ(0); }\n.post:hover {\n    cursor: pointer; }\n.post:before {\n    content: \"\";\n    position: absolute;\n    z-index: -1;\n    left: 0;\n    right: 100%;\n    bottom: 0;\n    background: #00BFFF;\n    height: 4px; }\n.post:hover:before, .post:focus:before, .post:active:before {\n    right: 0; }\n.post-title {\n  font-size: 24px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap; }\n.post-date {\n  position: relative;\n  border-top: 1px solid #00BFFF;\n  margin-top: 10px; }\n.date {\n  position: relative;\n  width: -webkit-fit-content;\n  width: -moz-fit-content;\n  width: fit-content;\n  padding: 0 5px;\n  top: -6px;\n  left: 25px;\n  font-size: 12px;\n  text-align: center;\n  background: #FFFFFF; }\n.post-description {\n  position: relative;\n  overflow: hidden;\n  font-size: 18px;\n  font-family: \"Open Sans Condensed\", sans-serif;\n  text-align: justify;\n  line-height: 1.5em;\n  max-height: 6em;\n  padding-right: 15px;\n  white-space: pre-line; }\n.post-description:before {\n  content: '...';\n  position: absolute;\n  right: 0;\n  bottom: 0; }\n.post-description:after {\n  content: '';\n  position: absolute;\n  right: 0;\n  width: 1em;\n  height: 1em;\n  margin-top: 0.2em;\n  background: #FFFFFF; }\n"